@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;&display=swap);
body {
  margin: 0;
}

* {
    font-family: 'Rubik', sans-serif;
}

.logo {
    font-size: 24px;
    color: #fff;
    font-weight: 700;
    margin-left: 20px;
    z-index: 4;
}

.Navigation {
    color: #fff;
    z-index: 4;
}

body input + label {
    position: absolute;
    top: 20px;
    right: 30px;
    height: 20px;
    width: 15px;
    z-index: 5;
  }
  body input + label span {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    background: #fff;
    transition: 0.5s;
  }
  body input + label span:first-child {
    top: 3px;
  }
  body input + label span:last-child {
    top: 16px;
  }
  body label:hover {
    cursor: pointer;
  }
  body input:checked + label span {
    opacity: 0;
    top: 50%;
  }
  body input:checked + label span:first-child {
    opacity: 1;
    -webkit-transform: rotate(405deg);
            transform: rotate(405deg);
  }
  body input:checked + label span:last-child {
    opacity: 1;
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
  }
  body input ~ nav {
    background: rgb(33 33 48);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    z-index: 3;
    transition: 0.5s;
    transition-delay: 0.5s;
    overflow: hidden;
  }
  body input ~ nav > ul {
    text-align: center;
    position: absolute;
    top: 35%;
    left: 0;
    right: 0;
    padding: 0;
  }
  body input ~ nav > ul > li {
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0s;
  }
  body input ~ nav > ul > li > a {
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-family: sans-serif;
    display: block;
    padding: 30px;
  }
  body input:checked ~ nav {
    height: calc(100% + 10px);
    transition-delay: 0s;
  }
  body input:checked ~ nav > ul > li {
    opacity: 1;
    transition-delay: 0.5s;
  }

.Name {
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size: 50px;
}

.Artist {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    font-size: 30px;
}

.Location {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    font-size: 20px;
}

@media only screen and (max-width: 445px) {
    .Name {
        max-width: 215px;
    }
}
.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
}

.slide_title {
    position: absolute;
    top: 20px;
    margin-left: 20px;
    width: auto;
    padding: 11px;
    height: auto;
    border-radius: 10px;
    background-color: rgb(33 33 48 / 73%);
    color: #fff;
    font-family: 'Rubik', sans-serif;
}

.slideLink {
    color: #fff;
    text-decoration: none;
}

.react-slideshow-container .nav {
    z-index: 2 !important;
}

@media only screen and (max-width: 920px) {
    .slide_image {
        height: calc(100vh - 50px);
    }
}

@media only screen and (max-width: 920px) {
    .Text_Container {
        width: 100% !important;
        height: calc(100vh - 60px) !important;
    }
    .Image_Container {
        width: 100% !important;
    }

    .Home_Container {
        flex-direction: column !important;
    }
}
.Footer_Content {
    width: 33.33%;
    height: 100%;
    display: flex;
    align-items: center;
    font-family: 'Rubik', sans-serif;
}

.Social_Media_Icon {
    width: 25px;
    -webkit-filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(184deg) brightness(108%) contrast(102%);
            filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(184deg) brightness(108%) contrast(102%);
    padding-right: 15px;
}

@media only screen and (max-width: 450px) {
    .Footer_Content {
        font-size: 3vw;
    }

    .Social_Media_Icon {
        width: 5vw;
    }
}
