@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;&display=swap');

.Footer_Content {
    width: 33.33%;
    height: 100%;
    display: flex;
    align-items: center;
    font-family: 'Rubik', sans-serif;
}

.Social_Media_Icon {
    width: 25px;
    filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(184deg) brightness(108%) contrast(102%);
    padding-right: 15px;
}

@media only screen and (max-width: 450px) {
    .Footer_Content {
        font-size: 3vw;
    }

    .Social_Media_Icon {
        width: 5vw;
    }
}